/* add normalize */
@import '../../../node_modules/normalize-scss/sass/normalize';

$aarp-red: #c03a38;
$aarp-blue: #2577A1;

.modal-dialog.modal-xl {
  height: calc(100% - 3.75rem);
}

.modal-content {
  height: 100%;
}

.modal {
  z-index: 1065;
}

.modal-backdrop {
  z-index: 1065;
}

.popover {
  max-width: 100px;
}

//Components
header {
  .navbar {
    padding-bottom: 40px;
    background: $aarp-red;

    .navigation-bar {
      background-color: white;
      position: absolute;
      left: 0;
      right: 0;
      top: 48px;

      ul {
        margin-bottom: 0;
        list-style-type: none;

        li {
          padding: 10px;

          a {
            font-weight: bold;
            text-transform: capitalize;
            color: black;

            &:hover {
              text-decoration: none;
            }
          }
        }
      }

    }
  }

}

.forum-feed {
  .forum-feed__canvas-container {
    background: url('../img/bg-texture.jpg') no-repeat;
    background-size: cover;
  }
}

.discussion-box,
.single-discussion {
  .rectangle {
    height: 20px;
    width: 70px;
    border-radius: 8px;
    background-color: $aarp-blue;
  }

  .discussion-box__header,
  .single-discussion__header {
    height: 45px;
    font-size: 14px;

    img {
      height: 100%;
      width: auto;
      margin-right: 10px;
    }

    .text-muted {
      text-transform: uppercase;
    }
  }
}